import React,{useState} from "react";
import NoteAddIcon from '@material-ui/icons/NoteAdd';
import Fab from '@material-ui/core/Fab';
import Zoom from '@material-ui/core/Zoom';

function CreateArea(props){
  const[isExpanded,setIsExpanded] = useState(false)

  const [note, setNote] = useState({
    title:"",
    content:""
  });

function handleChange(event){
const{name,value} = event.target;
setNote(prevNote=>{
return{
...prevNote,
[name]: value
};

})

};

function submitNote(event){
  props.onAdd(note);
  setNote({
    title:"",
    content:""
  })
  setIsExpanded(false)
  event.preventDefault();



}

function handleClick(){
setIsExpanded(true)

}

return (
<div className="formDiv" >
<form className="createNote">
{isExpanded? <input name="title" placeholder="Title" value={note.title} onChange={handleChange}/>: null}
<textarea onClick={handleClick} name="content" placeholder="Take a note..."rows={isExpanded? 4:1} value={note.content} onChange={handleChange}/>
<Zoom in={isExpanded}>
<Fab onClick={submitNote}>
<NoteAddIcon/>
</Fab>
</Zoom>
</form>
</div>

)


}


export default CreateArea;
